  <div class="home">
    <div id="canvas"></div>
    <section class="hero is-fullheight">
      <div class="hero-body">
        <div class="container centered has-text-centered">
          <h1 class="title noselect">
            <span class="first-name">Winston</span>
            <span class="last-name">Smith</span>
          </h1>
          <h2 class="subtitle noselect">
            <a href="/projects" class="projects">projects</a>|
            <a href="/sketches" class="sketches">sketches</a>|
            <a href="/blog" class="blog">blog</a>
            <!-- <a href="/about" class="sketches">sketches</a> |
            <a href="/blog" class="blog">blog</a>-->
