<script>
// @ is an alias to /src
// import HelloWorld from "@/components/HelloWorld.vue";

export default {
  name: 'home',
  mounted() {
    class Ball {
      constructor({
        sketch = null,
        x = 0,
        y = 0,
        r = 1,
        dx = 1,
        dy = 1,
        random = false
      } = {}) {
        this.sketch = sketch
        if (random) {
          ;(this.x = this.sketch.random(40, this.sketch.width - 40)),
            (this.y = this.sketch.random(40, this.sketch.height - 40)),
            (this.r = this.sketch.random(12, 40)),
            (this.dx = this.sketch.random(-2, 2)),
            (this.dy = this.sketch.random(-2, 2))
        } else {
          this.x = x
          this.y = y
          this.r = r
          this.dx = dx
          this.dy = dy
        }
        this.sketch.push()
        this.sketch.colorMode(this.sketch.HSB, 100, 100, 100)
        this.color = this.sketch.color(this.sketch.random(100), 85, 80)
        this.sketch.pop()
      }

      draw() {
        this.sketch.fill(this.color)
        this.sketch.circle(this.x, this.y, this.r)
        this.move()
      }

      move() {
        if (
          this.x + this.r >= this.sketch.width + this.r / 2 ||
          this.x <= this.r / 2
        ) {
          this.dx *= -1
          // this.newColor({ random: true })
        }
        if (
          this.y + this.r >= this.sketch.height + this.r / 2 ||
          this.y <= this.r / 2
        ) {
          this.dy *= -1
          // this.newColor({ random: true })
        }
        this.x += this.dx
        this.y += this.dy
      }

      newColor({ value = 'red', random = true }) {
        if (random) {
          this.sketch.push()
          this.sketch.colorMode(this.sketch.HSB, 100, 100, 100)
          this.color = this.sketch.color(this.sketch.random(100), 80, 80)
          this.sketch.pop()
        } else {
          this.color = value
        }
      }
    }

    const P5 = require('p5')
    let s = function(sketch) {
      let ballCount = 3
      let balls = []
      sketch.setup = function() {
        sketch.createCanvas(innerWidth, innerHeight)
        sketch.frameRate(30)
        sketch.noStroke()
        sketch.colorMode(sketch.HSB, 100, 100, 100)
        sketch.fill(sketch.random(100), 80, 80)
        for (let i = 0; i < ballCount; i++) {
          balls.push(
            new Ball({
              sketch: sketch,
              random: true
            })
          )
        }
      }

      sketch.draw = function() {
        sketch.clear()
        // sketch.circle(x, y, r)
        // x += dx
        // y += dy
        for (let ball of balls) {
          ball.draw()
        }
        if (sketch.frameCount % 50 == 0 && ballCount <= 30) {
          balls.push(new Ball({ sketch: sketch, random: true }))
          ballCount += 1
        }
      }

      sketch.mouseClicked = function() {
        balls.push(new Ball({ sketch: sketch, random: true }))
        ballCount += 1
      }

      sketch.windowResized = function() {
        sketch.resizeCanvas(innerWidth, innerHeight)
      }
    }
    new P5(s, 'canvas')
  }
  // components: {
  //   HelloWorld
  // }
}
</script>

